@import url(https://fonts.googleapis.com/css?family=Ubuntu+Mono);
@import url(https://fonts.googleapis.com/css?family=VT323);
@import url(https://fonts.googleapis.com/css?family=Ubuntu+Mono);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans);
@import url(https://fonts.googleapis.com/css?family=Ubuntu+Mono);

body{
  background-image: url(/static/media/my_background_4.f74da163.png);
  background-repeat: repeat;
}

.wrapper{
  /* Background pattern from Toptal Subtle Patterns */
  background:
  url(data:image/png;base64,);
  background-repeat: repeat;
  box-shadow: 10px 10px 20px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  height: 100%;
}

#coder{
  font-family: 'VT323', monospace;
  font-size: 35px;
}

.copyright{
  font-family: 'Ubuntu Mono', monospace;
  font-size: 14pt;
  text-align: center;
  margin-bottom: 5px;
  color: #494949;
}

@media (min-width: 1200px) {
  .wrapper{
    margin-right: 20%;
    margin-left: 20%;
    height: 100%;
  }
}

.content{
  margin-top: 5%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas:
  "experience_button about_button";
}

.experience_button{
  grid-area: experience_button;
  justify-self: right;
  background-color: white;
  color: black;
  border: 2px solid #e7e1f5;
  color: black;
  width: 90%;
  height: 40px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  transition-duration: 0.4s;
  cursor: pointer;
  font-family: 'Noto Sans', sans-serif;
  color: #494949;
}

.experience_active{
  background-color: #e7e1f5;
}

.about_button{
  grid-area: about_button;
  justify-self: left;
  background-color: white;
  color: black;
  border: 2px solid #e7e1f5;
  border-left: none;
  color: black;
  width: 90%;
  height: 40px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  transition-duration: 0.4s;
  cursor: pointer;
  font-family: 'Noto Sans', sans-serif;
  color: #494949;
}

.about_active{
  background-color: #e7e1f5;
}

.exp_text{
  color: #494949;
}

hr{
  margin-left: 2em;
  margin-right: 2em;
}

#wcroc_link{
  text-decoration: none;
  color: #663366;
}

.experience_project_wrapper{
  margin-top: 10px;
  position: relative;
  display: grid;
  grid-template-rows: auto;
  grid-template-areas:
  "projectTitle"
  "projectContent_text"
  "project_img";
}

.project_img_src{
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.experience_coop_wrapper{
  margin-top: 10px;
  position: relative;
  display: grid;
  grid-template-rows: auto;
  grid-template-areas:
  "title"
  "content_text"
  "coop_img";
}

.title{
  grid-area: title;
  font-family: 'Ubuntu Mono', monospace;
  font-size: 18pt;
}

.content_text{
  grid-area: content_text;
  font-family: 'Ubuntu Mono', monospace;
  font-size: 14pt;
}

.coop_img{
  grid-area: coop_img;
  justify-self: center;
}

.projectTitle{
  grid-area: projectTitle;
  font-family: 'Ubuntu Mono', monospace;
  font-size: 18pt;
}

.projectContent_text{
  grid-area: projectContent_text;
  font-family: 'Ubuntu Mono', monospace;
  font-size: 14pt;
}

.project_img{
  grid-area: project_img;
  justify-self: center;
  width: 90%;
  max-width: 455px;
}

@media (min-width: 1400px) {
  .projectTitle{
    grid-area: projectTitle;
    font-family: 'Ubuntu Mono', monospace;
    font-size: 20pt;
    margin-left: 2px;
  }

  .date{
    grid-area: date;
    font-family: 'Ubuntu Mono', monospace;
    font-size: 18pt;
    margin-left: 2px;
  }

  .projectContent_text{
    grid-area: projectContent_text;
    font-family: 'Ubuntu Mono', monospace;
    font-size: 16pt;
    margin-top: auto;
    margin-bottom: auto;
  }

  .experience_project_wrapper{
    margin-top: 3%;
    position: relative;
    display: grid;
    grid-template-columns: 1.5fr 1fr;
    grid-template-rows: auto;
    grid-template-areas:
    "projectTitle ."
    "projectContent_text project_img"
    "projectContent_text project_img";
  }

  .title{
    grid-area: title;
    font-family: 'Ubuntu Mono', monospace;
    font-size: 20pt;
    margin-left: 2px;
  }

  .content_text{
    grid-area: content_text;
    font-family: 'Ubuntu Mono', monospace;
    font-size: 16pt;
  }

  .experience_coop_wrapper{
    margin-top: 3%;
    position: relative;
    display: grid;
    grid-template-columns: 1.5fr 1fr;
    grid-template-rows: auto;
    grid-template-areas:
    "title ."
    "content_text coop_img"
    "content_text coop_img";
  }

  .experience_res_assist_wrapper{
    margin-top: 5%;
    margin-bottom: 5%;
  }

  .experience_ta_wrapper{
    margin-top: 5%;
    margin-bottom: 5%;
  }
}

.ab_text{
 font-family: 'Noto Sans', sans-serif;
 color: #494949;
}

.about_me_intro{
  margin: 5%;
  font-size: 16pt;
  text-align: center;
}

.ab_skills_header{
  text-align: center;
  font-size: 20pt;
}

li{
  margin-bottom: 5px;
}

.icon_title{
  text-align: center;
  font-size: 12pt;
}

.icons{
  display: grid;
  justify-content: center;
  grid-template-areas:
  "pair_prog"
  "agile_dev"
  "test_driven_dev"
  "cont_inter";
}

.pair_prog{
  grid-area: pair_prog;
  height: 300px;
  width: 200px;
}

.test_driven_dev{
  grid-area: test_driven_dev;
  height: 300px;
  width: 200px;
}

.cont_inter{
  grid-area: cont_inter;
  height: 300px;
  width: 200px;
}

.agile_dev{
  grid-area: agile_dev;
  height: 300px;
  width: 200px;
}

@media (min-width: 1400px) {
  .about_me_intro{
    font-size: 16pt;
  }

  .my_knowledge{
    font-size: 14pt;
  }

  .ab_skills_header{
    text-align: center;
    font-size: 20pt;
  }

  .icon_title{
    text-align: center;
    font-size: 16pt;
  }
}

@media (min-width: 1000px){
  .icons{
    display: grid;
    justify-content: center;
    grid-column-gap: 20%;
    grid-template-areas:
    "pair_prog agile_dev "
    "test_driven_dev cont_inter";
  }
}

.avatar {
  grid-area: avatar;
  justify-self: center;
  font-family: 'Ubuntu Mono', monospace;
  font-size: 7pt;
  webkit-touch-callout: none;
  user-select: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.text_ {
  grid-area: text_;
  justify-self: center;
  text-align: center;
  font-family: 'Ubuntu Mono', monospace;
  font-size: 20pt;
  margin-left: 2%;
  margin-right: 2%;
  margin-top: 3%;
  color: #494949;
}

.header{
  display: grid;
  grid-column-gap: 20px;
  grid-template-areas:
  "avatar"
  "text_"
  "contacts";
}

.contacts{
  margin-top: 5%;
  grid-area: contacts;
  text-align: center;
  color: #494949;
}

.contact_icon{
  font-size: 45px;
  color: black;
  margin-left: 10px;
  margin-right: 10px;
  text-align: center;
  color: #494949;
}

.contact_icon:hover{

  color: grey;
  transition-duration: 0.4s;
}

@media (min-width: 1500px) {
  .header {
    position: relative;
    display: grid;
    grid-column-gap: 20px;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    grid-template-areas:
    "avatar  text_"
    "contacts  contacts"
    ;
  }

  .text_{
    margin-top: 20%;
    text-align: center;
  }

  .avatar {
    margin-top: 5%;
    grid-area: avatar;
    justify-self: center;
    font-family: 'Ubuntu Mono', monospace;
    font-size: 7pt;
    webkit-touch-callout: none;
    user-select: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
  }
}

